import Vue from 'vue'
import App from './App'
import VueRouter from 'vue-router'
import fastclick from 'fastclick'
import YDUI from 'vue-ydui'; /* 相当于import YDUI from 'vue-ydui/ydui.rem.js' */

import axios from 'axios';
import VueAxios from 'vue-axios'
import cookie from './static/js/cookie'

// 导入弹窗
import { Confirm, Alert, Toast, Notify, Loading } from 'vue-ydui/dist/lib.px/dialog';



fastclick.attach(document.body)

import 'vue-ydui/dist/ydui.px.css'

Vue.use(VueAxios, axios)
Vue.use(YDUI)
Vue.use(VueRouter)
Vue.config.productionTip = false
Vue.prototype.$dialog = {
  confirm: Confirm,
  alert: Alert,
  toast: Toast,
  notify: Notify,
  loading: Loading,
}


const router = new VueRouter({
  mode:'history',
  routes: [
    {
      path: '/',
      component: resolve => require(['./page/home/home.vue'], resolve) /* <== 主要代码是这 */

    },
      {
          path: '/login',
          component: resolve => require(['./page/login/login.vue'], resolve) /* <== 主要代码是这 */
      },
      {
          path: '/register',
          component: resolve => require(['./page/register/register.vue'], resolve) /* <== 主要代码是这 */
      },
      {
        path: '/setting',
        component: resolve => require(['./page/setting/setting.vue'], resolve), /* <== 主要代码是这 */
        meta: {
          title: 'My setting',
          requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
        }
      },
      {
        path: '/roomie',
        component: resolve => require(['./page/roomie/roomie.vue'], resolve), /* <== 主要代码是这 */
      
      },
      {
        path: '/saved',
        component: resolve => require(['./page/saved/saved.vue'], resolve) /* <== 主要代码是这 */
      },
      {
        path: '/upload',
        component: resolve => require(['./page/upload/upload.vue'], resolve),
        meta: {
          title: 'upload my place',
          requireAuth: true // 添加该字段，表示进入这个路由是需要登录的
        }
      },
      {
        path: '/map',
        component: resolve => require(['./page/map/map.vue'], resolve) /* <== 主要代码是这 */
      }
  ]
});

router.beforeEach((to, from, next) => {
  // 设置标题
  // document.title = to.meta.title
  // store.commit('UPDATE_TITLE', to.meta.title)
  // loading
  // store.commit('UPDATE_LOADING', true)
// 这里设置如果要求登录的地方就跳到登录页面
  if (to.matched.some(r => r.meta.requireAuth)) {
    // console.log(cookie.getCookie('token'))
      if (cookie.getCookie('token')) {
        console.log('有cookie')
          next()
      } else {
        console.log('没有cookie')
          next({
              path: '/login',
              query: { redirect: to.fullPath }
          })
      }
  } else {
      next()
  }
})




/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



