  name: 'App',
  components: {
    Vfooter,
    GridsGroup,
    GridsItem,
    Vhome

  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "./common/stylus/reset"
#app
  font-family: 'Avenir', Helvetica, Arial, sans-serif
  -webkit-font-smoothing: antialiased
  -moz-osx-font-smoothing: grayscale
  text-align: center
  color: #2c3e50
  font-size :16px
  height :100%
body{
  padding-bottom:50px;
  z-index :-10;
}
.yd-flexview{z-index:10}
</style>



